import { createApp } from 'vue';

import { ThemeInterface } from "./theme/theme"
const theme = import(`./theme/${_THEME_}/theme`);
theme.then((module) => {
    const theme: ThemeInterface = module.default;
    const app = createApp(theme.component);
    app.use(theme.router);

    theme.router.isReady().then(() => app.mount('#app'))
});
